body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  line-height: 1.4;
  color: $text-color;
  letter-spacing: 0.03em;
  background-color: $bg-color;
  font-family: Poppins, sans-serif;
  // font-size: 4rem;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1320px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.logo {
  cursor: pointer;
  max-width: 15rem;
  width: 100%;
  z-index: 999;
}

.main {
  flex-grow: 1;
}

.section {
  min-height: 100vh;
  padding: 10rem 9% 2rem;

  @include media-2 {
    padding: 10rem 3% 2rem;
  }
}

.title {
  font-size: 5.6rem;
  font-weight: 700;
  line-height: 1.3;
  & span {
    color: $main-color;
  }

  @include media-3 {
    font-size: 5rem;
  }
}

.subtitle {
  font-size: 3.2rem;
  font-weight: 700;
}

.heading {
  text-align: center;
  font-size: 4.5rem;
  & span {
    color: $main-color;
  }
}

.btn {
  display: inline-block;
  padding: 1rem 2.8rem;
  background-color: $main-color;
  border-radius: 4rem;
  box-shadow: 0 0 1rem $main-color;
  font-size: 1.6rem;
  color: $second-bg-color;
  letter-spacing: 0.1rem;
  font-weight: 600;
  transition: 0.5s ease;

  &:hover {
    box-shadow: none;
  }
}
